import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'
import styles from '../assets/css/team.module.css'
function Team(props) {
  const data = useStaticQuery(graphql`
    {
      allFile(
        filter: {
          sourceInstanceName: { eq: "images" }
          relativeDirectory: { eq: "Team" }
        }
      ) {
        edges {
          node {
            id
            name
            childImageSharp {
              fluid(quality: 100) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
      }
    }
  `)
  const filteredData = {}
  data.allFile.edges.forEach(
    edge => (filteredData[edge.node.name] = edge.node.childImageSharp.fluid)
  )
//   console.log('HELLI REAM', filteredData)
  return (
    <article
      id="team"
      className={`${props.article === 'team' ? 'active' : ''} ${
        props.articleTimeout ? 'timeout' : ''
      }`}
      style={{ display: 'none' }}
    >
      <h2 className="major">Meet Our Team</h2>
      <h2 style={{ textAlign: 'center', margin: '40px 0' }}>Founders</h2>
      <div className={styles.row}>
        <div className={styles.card}>
          <div className={styles.cardImg}>
            <Img style={{ borderRadius: '50%' }} fluid={filteredData.zufi} />
          </div>
          <div className={styles.cardBody}>ZufiShan Pasha</div>
        </div>

        <div className={styles.card}>
          <div className={styles.cardImg}>
            <Img style={{ borderRadius: '50%' }} fluid={filteredData.shehzar} />
          </div>
          <div className={styles.cardBody}>Shehzar Sheriff</div>
        </div>

        <div className={styles.card}>
          <div className={styles.cardImg}>
            <Img style={{ borderRadius: '50%' }} fluid={filteredData.zeeshan} />
          </div>
          <div className={styles.cardBody}>Zeeshan Syed Javid</div>
        </div>
      </div>

      <h2 style={{ textAlign: 'center', margin: '40px 0' }}>Core Team</h2>
      <div className={styles.row}>
        <div className={styles.card}>
          <div className={styles.cardImg}>
            <Img style={{ borderRadius: '50%' }} fluid={filteredData.SHO} />
          </div>
          <div className={styles.cardBody}>Shoaib Alyaan</div>
        </div>

        <div className={styles.card}>
          <div className={styles.cardImg}>
            <Img style={{ borderRadius: '50%' }} fluid={filteredData.SHO} />
          </div>
          <div className={styles.cardBody}>ZufiShan</div>
        </div>

        <div className={styles.card}>
          <div className={styles.cardImg}>
            <Img style={{ borderRadius: '50%' }} fluid={filteredData.SHO} />
          </div>
          <div className={styles.cardBody}>ZufiShan</div>
        </div>
      </div>

     
      {props.close}
    </article>
  )
}

export default Team
