import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'

function Work(props) {
  const data = useStaticQuery(graphql`
    {
      allFile(
        filter: {
          sourceInstanceName: { eq: "images" }
          relativeDirectory: { eq: "Work" }
        }
      ) {
        edges {
          node {
            id
            name
            childImageSharp {
              fluid(maxHeight:300,quality: 100) {
                ...GatsbyImageSharpFluid_withWebp
                ...GatsbyImageSharpFluidLimitPresentationSize
              }
            }
          }
        }
      }
    }
  `)
  const filteredData = {}
  data.allFile.edges.forEach(
    edge => (filteredData[edge.node.name] = edge.node.childImageSharp.fluid)
  )
  // console.log('WORK', filteredData)
  return (
    <article
      id="work"
      className={`${props.article === 'work' ? 'active' : ''} ${
        props.articleTimeout ? 'timeout' : ''
      }`}
      style={{ display: 'none' }}
    >
      <h2 className="major">Work</h2>
      <span className=" main">
        <Img style={{ borderRadius:"4px",margin:"25px auto",height: '500px' }} fluid={filteredData.image1} />
      </span>
      <p>
        We started our journey making our very first delivery of fresh milk.
      </p>
      <span className=" main">
        <Img style={{ borderRadius:"4px",margin:"25px auto",height: '500px' }} fluid={filteredData.Ration2} />
      </span>
      <p>
        {' '}
        We started with about <b>60 liters of milk</b> and were able to provide
        milk for <b>70 Families</b>
      </p>
      <span className=" main">
        <Img style={{ borderRadius:"4px",margin:"25px auto",height: '500px' }} fluid={filteredData.Day1_crowd} />
      </span>
      <p>
        Though it seemed impossible at the time. Thanks to our milk angels, who
        donated and helped us to stay active, we have been able to increase our
        inventory to about <b>500 liters of milk a day!</b>
      </p>
      <span className=" main">
        <Img style={{ borderRadius:"4px",margin:"25px auto",height: '500px' }} fluid={filteredData.RationLast} />
      </span>
      <p>
        Effectively avergaed to feed about <b>1300 children a day</b> by Day 47.
      </p>
      <span className=" main">
        <Img style={{ borderRadius:"4px",margin:"25px auto",height: '500px' }} fluid={filteredData.Day47} />
      </span>
      <p>
        We are still continuing our journey. We hope you'll decide to be a part
        of this change as well!
      </p>
      <p>If you do then pitch in! Lets make a brighter future together!</p>
      <span className=" main">
        <Img style={{ borderRadius:"4px",margin:"25px auto",height: '500px' }} fluid={filteredData.smile} />
      </span>
      {props.close}
    </article>
  )
}

export default Work
