import PropTypes from 'prop-types'
import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'
import Work from './Work'
import Team from './Team'
import More from './More'
import Contact from './Contact'
import pic01 from '../images/pic01.jpg'
import pic02 from '../images/pic02.jpg'
import pic03 from '../images/pic03.jpg'
import styles from '../assets/css/main.module.css'
function Main(props) {
  const data = useStaticQuery(graphql`
    {
      allFile(
        filter: {
          sourceInstanceName: { eq: "images" }
          relativePath: { eq: "MILKLOGO.png" }
        }
      ) {
        edges {
          node {
            id
            name
            childImageSharp {
              fluid(quality: 100) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
      }
    }
  `)
  const MilkLogo = data.allFile.edges.map(edge => edge.node.childImageSharp)
  // console.log('In MAIN', MilkLogo[0].fluid)
  let close = (
    <div
      className="close"
      onClick={() => {
        props.onCloseArticle()
      }}
    ></div>
  )
  return (
    <div
      className={styles.wrapper}
      ref={props.setWrapperRef}
      id="main"
      style={props.timeout ? { display: 'flex' } : { display: 'none' }}
    >
      <article
        id="intro"
        className={`${props.article === 'intro' ? 'active' : ''} ${
          props.articleTimeout ? 'timeout' : ''
        }`}
        style={{ display: 'none', background: 'rgb(39 202 249 / 86%)' }}
      >
        <h2 className="major">About</h2>
        <span className="image main">
          <Img
            style={{ opacity: '.75 !important' }}
            fluid={MilkLogo[0].fluid}
          />
          {/* <img src={pic01} alt="" /> */}
        </span>
        <h2>What is Mission Milk?</h2>
        <p>
          Mission Milk is simply put a milk bank. Our goal us to provide
          children with the most basic source of nutrition that we have all
          grown up with as indian kids, MILK.
        </p>
        <h2>Why Milk?</h2>
        <p>
          It all started with an effort to deliver the basic rations to as many
          families as we could. During one such visit we realized at a single
          settlement about 50 families, there is an average of 4 children per
          family aged between 10 months to 10 years. WHile discussing their
          needs we understood that although most of the adults needs were being
          met through the rations being provided, the children still needed
          milk.
        </p>
        <h2>How Does Mission Milk Work?</h2>
        <p>
          if you ever wondered how you do you part in helping out the people on
          need during this period of lock down in the safest and most efficient
          way possible, this is it. We need you to enable us to help these
          children on your behalf. We are looking to collect donations
          corresponding to the value of per liter of milk. It is imperitive that
          you, our donors, understand that this is a daily requirement. Our
          supply will vary day to day as per the requests that we receive and
          funds raised. We intend on providing fresh milk, not only to the
          families we are already helping but any and all organisations. NGO's
          and other such amazing people already providing support to the needy.
        </p>
        {close}
      </article>

      <Work
        close={close}
        article={props.article}
        articleTimeout={props.articleTimeout}
      />
      <More
        close={close}
        article={props.article}
        articleTimeout={props.articleTimeout}
      />

      <Team
        close={close}
        article={props.article}
        articleTimeout={props.articleTimeout}
      />
      <Contact
        close={close}
        article={props.article}
        articleTimeout={props.articleTimeout}
      />
    </div>
  )
}

export default Main

