import React, { useState, useEffect } from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'
import BackgroundImage from 'gatsby-background-image'
import styles from '../assets/css/background.module.css'

function Background() {
  const data = useStaticQuery(graphql`
    {
      allFile(
        filter: {
          sourceInstanceName: { eq: "images" }
          relativeDirectory: { eq: "bg" }
        }
      ) {
        edges {
          node {
            id
            name
            childImageSharp {
              fluid(quality: 100) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
      }
    }
  `)
  const filteredData = []
  data.allFile.edges.forEach(edge => {
    filteredData.push(edge.node.childImageSharp.fluid)
  })
  // console.log('CHECL', filteredData)
  const [index, setIndex] = useState(0)
  useEffect(() => {
    const handleChange = () => {
      document.getElementById('IMAGE').style.opacity = 0
      setTimeout(() => {
        setIndex(state => (state + 1) % filteredData.length)
        setTimeout(() => {
          document.getElementById('IMAGE').style.opacity = 1
        }, 100)

        // setIndex(state => (state + 1) % filteredData.length)
      }, 300)
    }
    const timer = setInterval(() => handleChange(), 2500)
    return () => {
      clearInterval(timer)
    }
  }, [])

  return (
    <div id="IMAGE" className={styles.container}>
      <BackgroundImage className={styles.img} fluid={filteredData[index]} />
    </div>
  )
}

export default Background
